.contactsPageTitle {
   display: flex;
   height: 55px;
   justify-content: center;
   align-items: center;
   font-size: 10px;
   font-weight: 900;
   margin: 0;
   color: #7b7a7a;

   @media screen and (min-width: 768px) {
      height: 75px;
      font-size: 13px;
   };
   @media screen and (min-width: 1200px) {
      height: 95px;
      font-size: 15px;
   };
}

.fadeOut {
   opacity: 0; 
   transition: opacity .1s ease-in;
 }

 .mapBox {
   position: relative;
   display: flex;
   margin-left: auto;
   margin-right: auto;
   background:url("images/map44.jpg");
   /* background-attachment: fixed; */
   background-size: cover;
   background-position: center;
   background-repeat: no-repeat;
   /* margin-bottom: 70px; */
   height: 300px;
   z-index: 0;
   box-shadow: 0 0 20px 22px #00000064;

   @media screen and (min-width: 768px) {
      height: 500px;
   };
   @media screen and (min-width: 1200px) {
      height: 700px;

   };
}

.mapBox::after {
   content: "";
   position: absolute;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   background: #0707074e;
   z-index: -1;
   background-image: url("https://www.transparenttextures.com/patterns/white-wall-3-2.png");
}

