.contactsListBox {
   display: flex;
   height: 550px;
   justify-content: center;
   align-items: center;
  }


 .contactsList {
   display: grid;
   grid-template-columns: repeat(1, 1fr);
   grid-column-gap: 20px;
   grid-row-gap: 30px;
   width: 600px;
   padding: 0 50px;

  }
 
 .contactsListItem {
   /* position: relative; */
   display: flex;
   justify-content: space-between;
   align-items: center;
   height: 70px;
   width: 100%;
   color: #7b7b7b;
   font-size: 15px;
   border-radius: 11px;
   margin:  3px 0;
   box-shadow: 6px 6px 15px 2px #0000004f;
   background-color: #000000;
   background-image: url("https://www.transparenttextures.com/patterns/white-wall-3-2.png");
  }

.contactsListItem img {
   display: block;
   height: 30px;
   padding-left: 50px;
}

.contactsListItemDiscriptions {
   padding-right: 50px;
 
   color: #414141;

}
